import { DOM, PropTypes } from 'react';

const Meta = ({ author, createdAt, updatedAt, likesCount }) => (
  DOM.ul(
    { className: 'list-group center-block',  },
    DOM.li({ className: 'list-group-item' }, `: ${author}`),
    DOM.li({ className: 'list-group-item' }, `: ${createdAt}`),
    DOM.li({ className: 'list-group-item' }, `: ${updatedAt}`),
    DOM.li({ className: 'list-group-item' }, `: ${likesCount}`)
  )
);

Meta.defaultProps = {
  author: ' ',
  createdAt: ' ',
  updatedAt: ' ',
  likesCount: 0
};

Meta.propTypes = {
  author: PropTypes.string,
  createdAt: PropTypes.string,
  updatedAt: PropTypes.string,
  likesCount: PropTypes.number
};

export default Meta;


